/*------------------1-ITEM-------------------*/

.opening__container.anima1 .obj {
  animation-duration: 0.7s;
  animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
  animation-fill-mode: forwards;
  animation-delay: 1.1s;
}
.opening__container.anima1 .obj1 {
  animation-name: drop_anima_3-2;
}

/*------------------2-ITEMS-------------------*/

.opening__container.anima2 .obj {
  animation-duration: 0.7s;
  animation-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
  animation-fill-mode: forwards;
  animation-delay: 1.1s;
}
.opening__container.anima2 .obj1 {
  animation-name: drop_anima_2-1;
}
.opening__container.anima2 .obj2 {
  animation-name: drop_anima_2-2;
  animation-delay: 1.5s;
}
@media (max-width: 550px) {
  .opening__container.anima2 .obj1 {
    animation-name: media550-drop_anima_2-1;
  }
  .opening__container.anima2 .obj2 {
    animation-name: media550-drop_anima_2-2;
    animation-delay: 1.5s;
  }
}

@keyframes drop_anima_2-1 {
  100% {
    bottom: 75%;
    transform: translate(-50%, 50%);
    left: calc(50% - 100px);
    opacity: 1;
  }
}
@keyframes drop_anima_2-2 {
  100% {
    bottom: 75%;
    transform: translate(-50%, 50%);
    left: calc(50% + 100px);
    opacity: 1;
  }
}
@keyframes media550-drop_anima_2-1 {
